import React from 'react'
import {Link} from 'react-router-dom';
import {
    FacebookIcon,
    TwitterIcon,
    LinkedinIcon,
} from "react-share";

export default class Footer extends React.Component {
    render() {
        return (
            <div className='footer'>
                <div className='footer-links'>
                    <div className='footer-links-col'>
                        <Link to='/platform/webviewer'>3D Web Viewer</Link><br/>
                      {/*   <Link to='/platform/360panoramaviewer'>360 Panorama Viewer</Link><br/> */}
                        <Link to='/platform/augmentedreality'>Augmented reality</Link><br/>
                        <Link to='/platform/configurator'>Product personalization</Link><br/>
                        <Link to='/demos'>Demos</Link><br/>
                        <Link to='/blog/blog'>Blog</Link><br/>
                    </div>
                    <div className='footer-links-col'>
                        <Link to='/contact'>Contact Us</Link><br/>
                        <Link to='/about'>About</Link><br/>
                        <Link to='/help'>Help Center</Link><br/>
                    </div>
                    <div className='footer-links-col'>
                        <Link to='/termsOfService'>Terms of Service</Link><br/>
                        <Link to='/privacyPolicy'>Privacy Policy</Link><br/>
                    </div>                    
                </div>
                <div className='footer-social-links'>
                    <div>
                        <a href='https://www.facebook.com/ezyvuesoftware'>
                            <FacebookIcon size={32} />
                        </a>
                        <a href='https://twitter.com/EzyVueSoftware'>
                            <TwitterIcon size={32} />
                        </a>
                        <a href='https://www.linkedin.com/company/ezyvue'>
                            <LinkedinIcon size={32} />
                        </a>
                    </div>
                </div>
            </div>
        )
    }
}